import Vue from "vue";
import store from "@/store";
import App from "./App.vue";
Vue.directive('focus', {
    inserted: function (el) {
        el.focus();
    }
});
Vue.directive("select", {
    inserted: function (el) {
        el.select();
    }
});
new Vue({
    store: store,
    el: '#main',
    template: "<App/>",
    components: { App: App },
});
