import { Model } from "@/shared/model";
import { Direction } from "@/shared/types";
export default (function () {
    var model = new Model();
    var a = {
        label: "a",
        position: { x: 100, y: 200 }
    };
    var b = {
        label: "b",
        position: { x: 300, y: 200 }
    };
    var c = {
        from: a,
        to: b,
        direction: Direction.Left,
        read: "1",
        write: "0"
    };
    var d = {
        from: b,
        to: a,
        direction: Direction.Left,
        read: "1",
        write: "2"
    };
    model.addState(a);
    model.addState(b);
    model.start = a;
    model.addTransition(c);
    model.addTransition(d);
    return model;
});
